const datePickerLanguage = {
    startDay: 1,
    customDays: ["", "", "", "", "", "", ""],
    customMonths: ["", "", "", "", "", "", "", "", "", "", "", ""],
    overlayButton: "",
    overlayPlaceholder: " "
};

function renderDateUpdate(date, dayNode, monthNode, monthsForUpdate, yearNode) {
    const {day, month, year} = {
        day: date.getDate(),
        month: date.getMonth(),
        year: date.getFullYear()
    };

    dayNode.textContent = day;
    monthNode.textContent = monthsForUpdate[month];
    yearNode.textContent = year;
}

module.exports = function () {
    const dateInputs = [...document.querySelectorAll("input[data-datepicker]")];
    const breakpoint = 1024;

    const monthsForUpdate = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
    ];

    dateInputs.forEach(node => {
        const container = node.parentElement;

        const [dayNode, monthNode, yearNode] = [
            container.querySelector(".make-order-date-day span"),
            container.querySelector(".make-order-date-month span"),
            container.querySelector(".make-order-date-year span"),
        ];

        const currentDate = new Date();
        const picker = datepicker(node, {
            ...datePickerLanguage,

            formatter: (input, date, instance) => {
                input.value = date.toLocaleDateString("en-US", {
                    year: "numeric",
                    month: "2-digit",
                    day: "2-digit"
                });
            },

            onSelect: function (instance, date) {
                renderDateUpdate(date, dayNode, monthNode, monthsForUpdate, yearNode);
            }
        });

        renderDateUpdate(currentDate, dayNode, monthNode, monthsForUpdate, yearNode);
        picker.setDate(currentDate);
    });
};
